const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class Reglements extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
      }
  }

  componentDidMount() { };

  
  render() {

    return (
      <>
        {this.state.ok}
        <CContainer fluid>
          <Suspense fallback={loading}>
            <CFade>
              <div class="container">
                <div class="section-title" >
                  <h2 className="mt-2">Les règles de jeu de nos tournois</h2>
                </div>
                
                <div >
                  
                  <p>
                    <strong>Article 1</strong> : O-trrain dispose de ses propres règles de jeu et d’arbitrage pour la gestion de ses 
                    tournois. Ces règles s’inspirent beaucoup des règles FIFA mais sont souvent spécifiques surtout 
                    dans certains cas.
                  </p>
                  

                  <p>
                    <strong>Article 2</strong> : Les arbitres sur le terrain ont le dernier mot durant tout le temps du match en cours.
                  </p>
                  
                  
                  <p>
                    <strong>Article 3</strong> : En cas de refus d’un  joueur quelconque de se conformer à la décision d’un arbitre, il 
                    s’expose et expose son équipe à des sanctions de la part de o-terrain.
                  </p>
                  
                  
                  <p>
                    <strong>Article 4</strong>: Avant le début de chaque tournois, un mail envoyé aux participants pour leur envoyer 
                    quelques règles spécifiques qui sont partie intégrante de nos règles.   
                  </p>
                   
                  
                  <p>
                    <strong>Article 5</strong> : Les sanctions de o-terrain peuvent être pécuniaires mais aussi des exclusions temporaires
                    ou définitives de nos tournois.
                  </p>
                  
                  
                  <p>
                    <strong>Article 6</strong> : Tout participant à un de nos tournois accepte l’intégralité de nos règles notamment en 
                    cochant les cases à cocher à l’inscription.
                  </p>
                  
                  
                  <p>
                    <strong>Article 7</strong>: Les frais de participation demandé à l’inscription ne sont pas remboursable.
                  </p>
                  
                  
                  <p>
                    <strong>Article 8</strong> : En cas de litige entre équipe, il revient à o-terrain d’arbitrer et  aucune équipe, joueur ou 
                    supporter, sous peine de sanction n’a le droit d’essayer de le régler avec violence physique ou 
                    verbale à sa manière.
                  </p>
                  
                  
                  <p>
                    <strong>Article 9</strong>: O-terrain peux éventuellement fournir les premiers soins sur le terrain en cas de blessure 
                    mais ce n’est pas une obligation.
                  </p>
                  
                  
                  <p>
                    <strong>Article 10</strong> : Les terrains de jeu sont choisis par o-tarrain.
                  </p>
                  
                  
                  <p>
                    <strong>Article 11</strong> : En cas d’absence d’une équipe ou bien d’un retard de plus de 15 minutes, la victoire est 
                    donnée à l’équipe est est en place.
                  </p>
                  
                  
                  <p>
                    <strong>Article 12</strong> : Le temps de jeux par période est défini avant chaque tournois et il peut être ajuster s’il y
                    a un retard d’une ou des équipes.
                  </p>
                  
                  
                  <p className="mb-2">
                    <strong>Article 13 </strong>: Il est interdit de violer physiquement ou verbalement nos arbitre. Toute revendication 
                    peut être adresser à o-terrain via mail ou courrier. Les contrevenants s’exposent à une amende ou 
                    une exclusion de nos tournois.
                  </p>
                  
                </div>

                <button id="submit" className="mt-3 mb-4" >Retour</button>

              </div>
            </CFade>
          </Suspense>
        </CContainer>
      </>
    )
  }
}

export default Reglements
